import Vue from 'vue';
import VueRouter from 'vue-router';
import Vuex from 'vuex';
import VueResource from 'vue-resource';
import routes from './routes.js';
import Main from './../template/Main.vue';
import state from './store/states.js';
import getters from './store/getters.js';
import actions from './store/actions.js';
import mutations from './store/mutations.js';

Vue.use(VueResource);
Vue.use(VueRouter);
Vue.use(Vuex);
Vue.config.devtools = false;    // ConsoleDev Tool


/** Vuex **/
const store = new Vuex.Store({
  state,
  getters,
  actions,
  mutations
});

/**  **/
const router = new VueRouter({
  mode: 'history',
  base: '/',
  routes: routes,
  scrollBehavior (to, from, savedPosition) {
    // 
    let position = savedPosition;   // savedPosition
    if (null === position) {
      // 
      position = { x: 0, y: 0 };
    } else {
      // 
      position = store.getters.beforeMainPosition;
    }
    store.dispatch('transitionScroll', position); // 
    return position;  // return window.scrollTo 
  }
});
router.beforeEach((to, from, next) => {
  store.dispatch('setBeforeMainPosition', store.getters.mainPosition);
  // 
  next();  // beforeEach
});
router.afterEach((to, from) => {
  // 
  store.dispatch('hideDrawer');
});


// Vuemixin
Vue.mixin({
  mounted: function() {
    if ('undefined' !== typeof componentHandler) {
      // MDLJS
      const el = document.querySelector('.page-content') || document.querySelector('#app');
      componentHandler.upgradeElements(el);
    }
  }
});

// 
new Vue({
  store,
  router,
  render: h => h(Main)
}).$mount('#app');



